.title{
  
  color: #dd4b39 !important;
}
.modalContainer {
    width: 800px;
    height: 620px;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    z-index: 10;
  }

   .modalBackground {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    margin-left: auto;
    margin-right: auto;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    left: 0;
    top: 0;
  }

  .modalContainer .title {
    display: inline-block;
    text-align: center;
    margin-top: 10px;
    font-size: 30px;
    color: #555;
  }

  .modalContainer .closebtn{
    background-color: transparent;
    border: none;
    font-size: 30px;
    cursor: pointer;
    margin-right: 15px;
    margin-top: 10px;
  }

  .closebtn {
    margin-left: auto;
    margin-right: 2%;
  }

  .closebtn:hover{
    color: red;
  }

  .modalContainer .headerbody{
    margin: 15px 0;
    font-size: 30px;
    display: flex;
    justify-content: center;
    margin-top: 0;
  }

  .headerbody .titleNameSchool{
      text-align: center;
      justify-items: center;
  }

.logo img{
    border: 5px solid white;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    
}
.bodytitle .titleProfile{
  text-align: center;
  font-size: 20px;
  padding-top:10px;
}

.time-css{
  color: #fff;
  display: flex;
  flex-direction: column;
}

.fa {
  padding: 10px;
  font-size: 20px;
  width: 5%;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  margin: 20px auto;

}

.fa:hover {
  opacity: 0.7;
}

.fa-facebook {
  color: #fff;
  font-size: 20px;
  height: 36px;
  width: 36px;
  border-radius: 50%;
  margin: 0 8px;
  background-color: #4070f4;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  margin-top:15px;
  margin-left: 43%;
  margin-right: auto;
  justify-content: space-between;
}

.fa-google {
  background: #dd4b39;
  color: white;
  height: 36px;
  width: 36px;
  border-radius: 50%;
  margin: 0 8px;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  margin-top:15px;
  margin-left:5%;
  margin-right: auto;
  justify-content: space-between;
}

@media (max-width:450px){
  .iconsocial .fa{
    width: 10%;
    text-align: center;
    margin-left: 43%;
    margin-right: auto;
  }
}

@media (max-width:450px){
  .iconsocial .fa-facebook{
    width: 10%;
    text-align: center;
    margin-left: 35%;
    margin-right: auto;
    padding: auto;
  }
}

@media (max-width:450px){
  .iconsocial .fa-google{
    width: 10%;
    text-align: center;
    margin-right: 35%;
    margin-left: auto;
    float: right;
    padding: auto;
  }
}

@media (max-width:450px){
  .footer .btnCancel{
    margin-top: auto;
  }
}

@media (max-width:450px){
  .modalContainer{
    height: auto;
  }
}

@media (max-width:320px){
  .closebtn{
    margin-left: auto;
    margin-top: 125px;
  }
}

@media (max-width:320px){
  .headerbody .titleNameSchool{
    height: auto;
    width: auto;
  }
}

